}

.header{
    padding: 13px 0px 10px 20px;
    position: absolute;
    top: 0;
    width: 100%;
    background: #fff;
    z-index: 4;
}

.logo{
    width: 100px;
    margin-top: 5px;
}
.padding-removed{
    padding: 0;
    margin: 0;
}

.header-text{
    font-family: "Google Sans";
    text-align: right;
}

.header-txt-bold{
    font-size: 25px;
    color: #15171c;
}

.header-chapter-name{
    font-size: 15px;
    color: #666666;
}

.nav-bar{
    padding: 0rem;
    background-color: white;
    -webkit-filter: drop-shadow(0px 2px 10px rgb(214, 214, 214));
            filter: drop-shadow(0px 2px 10px rgb(214, 214, 214));
}

.nav-header{
    font-size: 1.2rem;
